import { create } from 'zustand';
import { RectangleElement, CanvasState, Mode, ViewportState } from '../types';

interface CanvasStore extends CanvasState {
  // 模式切换
  setMode: (mode: Mode) => void;
  
  // 元素管理
  addElement: (element: RectangleElement) => void;
  updateElement: (id: string, updates: Partial<RectangleElement>) => void;
  removeElement: (id: string) => void;
  
  // 选择管理
  selectElement: (id: string, multiSelect?: boolean) => void;
  clearSelection: () => void;
  
  // 视口管理
  setViewport: (viewport: Partial<ViewportState>) => void;
  
  // 网格显示
  toggleGrid: () => void;
  
  // 工具方法
  getElementById: (id: string) => RectangleElement | undefined;
  getSelectedElements: () => RectangleElement[];
  fitElementsToView: () => void;
}

const useCanvasStore = create<CanvasStore>((set, get) => ({
  // 初始状态
  mode: 'browse',
  elements: [
    {
      id: 'default_rect_1',
      x: 100,
      y: 100,
      width: 120,
      height: 80,
      fill: '#3498db',
      stroke: '#2980b9',
      strokeWidth: 2,
      rotation: 0
    },
    {
      id: 'default_rect_2',
      x: 250,
      y: 150,
      width: 100,
      height: 100,
      fill: '#e74c3c',
      stroke: '#c0392b',
      strokeWidth: 2,
      rotation: 15
    },
    {
      id: 'default_rect_3',
      x: 150,
      y: 280,
      width: 150,
      height: 60,
      fill: '#2ecc71',
      stroke: '#27ae60',
      strokeWidth: 2,
      rotation: 0
    }
  ],
  selectedElementIds: [],
  viewport: { x: 0, y: 0, scale: 1, width: 1200, height: 800 },
  showGrid: true,
  
  // 模式切换
  setMode: (mode) => set({ mode, selectedElementIds: [] }),
  
  // 元素管理
  addElement: (element) => set((state) => ({
    elements: [...state.elements, element]
  })),
  
  updateElement: (id, updates) => set((state) => ({
    elements: state.elements.map(el => 
      el.id === id ? { ...el, ...updates } : el
    )
  })),
  
  removeElement: (id) => set((state) => ({
    elements: state.elements.filter(el => el.id !== id),
    selectedElementIds: state.selectedElementIds.filter(selectedId => selectedId !== id)
  })),
  
  // 选择管理
  selectElement: (id, multiSelect = false) => set((state) => {
    if (state.mode !== 'edit') return state;
    
    if (multiSelect) {
      const isSelected = state.selectedElementIds.includes(id);
      return {
        selectedElementIds: isSelected
          ? state.selectedElementIds.filter(selectedId => selectedId !== id)
          : [...state.selectedElementIds, id]
      };
    } else {
      return { selectedElementIds: [id] };
    }
  }),
  
  clearSelection: () => set({ selectedElementIds: [] }),
  
  // 视口管理
  setViewport: (viewport) => set((state) => ({
    viewport: { ...state.viewport, ...viewport }
  })),
  
  // 网格显示
  toggleGrid: () => set((state) => ({ showGrid: !state.showGrid })),
  
  // 工具方法
  getElementById: (id) => get().elements.find(el => el.id === id),
  
  getSelectedElements: () => {
    const { elements, selectedElementIds } = get();
    return elements.filter(el => selectedElementIds.includes(el.id));
  },

  // 自动适配视口到所有元素
  fitElementsToView: () => {
    const { elements, viewport } = get();
    
    if (elements.length === 0) return;
    
    // 计算所有元素的边界框
    let minX = Infinity, minY = Infinity, maxX = -Infinity, maxY = -Infinity;
    
    elements.forEach(element => {
      const left = element.x;
      const top = element.y;
      const right = element.x + element.width;
      const bottom = element.y + element.height;
      
      minX = Math.min(minX, left);
      minY = Math.min(minY, top);
      maxX = Math.max(maxX, right);
      maxY = Math.max(maxY, bottom);
    });
    
    // 计算边界框的中心和尺寸
    const centerX = (minX + maxX) / 2;
    const centerY = (minY + maxY) / 2;
    const contentWidth = maxX - minX;
    const contentHeight = maxY - minY;
    
    // 添加一些边距（20%）
    const padding = 0.2;
    const paddedWidth = contentWidth * (1 + padding);
    const paddedHeight = contentHeight * (1 + padding);
    
    // 计算合适的缩放比例
    const scaleX = viewport.width / paddedWidth;
    const scaleY = viewport.height / paddedHeight;
    const scale = Math.min(scaleX, scaleY, 2); // 限制最大缩放为2倍
    
    // 计算视口位置，使内容居中
    const viewportX = centerX - viewport.width / (2 * scale);
    const viewportY = centerY - viewport.height / (2 * scale);
    
    // 更新视口
    set((state) => ({
      viewport: {
        ...state.viewport,
        x: viewportX,
        y: viewportY,
        scale: Math.max(scale, 0.1) // 限制最小缩放为0.1倍
      }
    }));
  }
}));

export { useCanvasStore };
export default useCanvasStore;