import React, { useRef, useEffect } from 'react';
import { Transformer } from 'react-konva';
import Konva from 'konva';
import useCanvasStore from '../store/canvasStore';

const TransformerComponent: React.FC = () => {
  const transformerRef = useRef<Konva.Transformer>(null);
  const { selectedElementIds, updateElement } = useCanvasStore();

  useEffect(() => {
    const transformer = transformerRef.current;
    if (!transformer) return;

    const stage = transformer.getStage();
    if (!stage) return;

    if (selectedElementIds.length === 0) {
      transformer.nodes([]);
      return;
    }

    // 获取选中的节点
    const selectedNodes = selectedElementIds
      .map((id: string) => stage.findOne(`#${id}`))
      .filter(Boolean) as Konva.Node[];

    transformer.nodes(selectedNodes);
    transformer.getLayer()?.batchDraw();
  }, [selectedElementIds]);

  // 处理变换结束
  const handleTransformEnd = () => {
    const transformer = transformerRef.current;
    if (!transformer) return;

    const nodes = transformer.nodes();
    
    nodes.forEach((node: any) => {
      const rect = node as Konva.Rect;
      const scaleX = rect.scaleX();
      const scaleY = rect.scaleY();

      // 重置缩放并更新尺寸
      rect.scaleX(1);
      rect.scaleY(1);

      updateElement(rect.id(), {
        x: rect.x(),
        y: rect.y(),
        width: Math.max(5, rect.width() * scaleX),
        height: Math.max(5, rect.height() * scaleY),
        rotation: rect.rotation()
      });
    });
  };

  return (
    <Transformer
      ref={transformerRef}
      boundBoxFunc={(oldBox: any, newBox: any) => {
        // 限制最小尺寸
        if (newBox.width < 5 || newBox.height < 5) {
          return oldBox;
        }
        return newBox;
      }}
      onTransformEnd={handleTransformEnd}
      enabledAnchors={[
        'top-left',
        'top-center',
        'top-right',
        'middle-right',
        'middle-left',
        'bottom-left',
        'bottom-center',
        'bottom-right'
      ]}
      rotateEnabled={true}
      borderStroke="#007bff"
      borderStrokeWidth={2}
      anchorFill="#007bff"
      anchorStroke="#0056b3"
      anchorSize={8}
    />
  );
};

export default TransformerComponent;