import React from 'react';
import { Layer, Line } from 'react-konva';
import useCanvasStore from '../store/canvasStore';

const GridLayer: React.FC = () => {
  const { viewport } = useCanvasStore();
  const gridSize = 20; // 网格大小(虚拟坐标)
  
  // 计算当前视口在虚拟空间中看到的范围
  const viewportLeft = viewport.x;
  const viewportTop = viewport.y;
  const viewportRight = viewport.x + viewport.width / viewport.scale;
  const viewportBottom = viewport.y + viewport.height / viewport.scale;
  
  // 扩展渲染范围，确保拖拽时网格连续
  const padding = gridSize * 5;
  const renderLeft = viewportLeft - padding;
  const renderTop = viewportTop - padding;
  const renderRight = viewportRight + padding;
  const renderBottom = viewportBottom + padding;
  
  // 计算网格线的起始位置(对齐到网格)
  const startX = Math.floor(renderLeft / gridSize) * gridSize;
  const endX = Math.ceil(renderRight / gridSize) * gridSize;
  const startY = Math.floor(renderTop / gridSize) * gridSize;
  const endY = Math.ceil(renderBottom / gridSize) * gridSize;
  
  const lines = [];
  
  // 垂直网格线
  for (let x = startX; x <= endX; x += gridSize) {
    // 转换为屏幕坐标
    const screenX = (x - viewport.x) * viewport.scale;
    const screenY1 = (startY - viewport.y) * viewport.scale;
    const screenY2 = (endY - viewport.y) * viewport.scale;
    
    lines.push(
      <Line
        key={`v-${x}`}
        points={[screenX, screenY1, screenX, screenY2]}
        stroke="#e0e0e0"
        strokeWidth={0.5}
        opacity={0.3}
        listening={false}
      />
    );
  }
  
  // 水平网格线
  for (let y = startY; y <= endY; y += gridSize) {
    // 转换为屏幕坐标
    const screenX1 = (startX - viewport.x) * viewport.scale;
    const screenX2 = (endX - viewport.x) * viewport.scale;
    const screenY = (y - viewport.y) * viewport.scale;
    
    lines.push(
      <Line
        key={`h-${y}`}
        points={[screenX1, screenY, screenX2, screenY]}
        stroke="#e0e0e0"
        strokeWidth={0.5}
        opacity={0.3}
        listening={false}
      />
    );
  }
  
  return (
    <Layer listening={false}>
      {lines}
    </Layer>
  );
};

export default GridLayer;