import React from 'react';
import useCanvasStore from '../store/canvasStore';
import { RectangleElement } from '../types';

const Toolbar: React.FC = () => {
  const { mode, addElement, showGrid, toggleGrid, viewport } = useCanvasStore();

  const generateId = () => `rect_${Date.now()}_${Math.random().toString(36).substr(2, 9)}`;
  
  const addRectangle = () => {
    // 在当前视口中心附近添加矩形
    const centerX = viewport.x + viewport.width / (2 * viewport.scale);
    const centerY = viewport.y + viewport.height / (2 * viewport.scale);
    
    const newRect: RectangleElement = {
      id: generateId(),
      x: centerX + (Math.random() - 0.5) * 200,
      y: centerY + (Math.random() - 0.5) * 200,
      width: 100,
      height: 80,
      fill: '#3498db',
      stroke: '#2980b9',
      strokeWidth: 2,
      rotation: 0
    };
    console.log('Adding rectangle:', newRect);
    console.log('Current viewport:', viewport);
    addElement(newRect);
  };

  return (
    <>
      <button 
        className="add-rectangle-btn" 
        onClick={addRectangle}
        disabled={mode === 'browse'}
        style={{
          opacity: mode === 'browse' ? 0.5 : 1,
          cursor: mode === 'browse' ? 'not-allowed' : 'pointer'
        }}
      >
        添加矩形
      </button>
      <button 
        className={`mode-button ${showGrid ? 'active' : ''}`}
        onClick={toggleGrid}
      >
        {showGrid ? '隐藏网格' : '显示网格'}
      </button>
      <span style={{ marginLeft: '10px', fontSize: '12px', color: '#666' }}>
        当前模式: {mode === 'browse' ? '浏览模式' : '编辑模式'}
      </span>
    </>
  );
};

export default Toolbar;