import React, { useRef } from 'react';
import { Rect } from 'react-konva';
import Konva from 'konva';
import useCanvasStore from '../store/canvasStore';
import { RectangleElement as RectangleElementType } from '../types';

interface Props {
  element: RectangleElementType;
}

const RectangleElement: React.FC<Props> = React.memo(({ element }) => {
  const rectRef = useRef<Konva.Rect>(null);
  const { 
    mode, 
    selectedElementIds, 
    selectElement, 
    updateElement,
    viewport
  } = useCanvasStore();

  const isSelected = selectedElementIds.includes(element.id);
  
  console.log('Rendering RectangleElement:', element.id, 'in mode:', mode);

  // 处理点击选择
  const handleClick = (e: Konva.KonvaEventObject<MouseEvent>) => {
    if (mode === 'edit') {
      e.cancelBubble = true;
      const multiSelect = e.evt.shiftKey;
      selectElement(element.id, multiSelect);
    }
  };

  // 处理拖拽
  const handleDragEnd = (e: Konva.KonvaEventObject<DragEvent>) => {
    if (mode === 'edit') {
      const rect = e.target as Konva.Rect;
      // 将屏幕坐标转换回虚拟坐标
      const virtualX = rect.x() / viewport.scale + viewport.x;
      const virtualY = rect.y() / viewport.scale + viewport.y;
      
      updateElement(element.id, {
        x: virtualX,
        y: virtualY
      });
    }
  };

  // 将虚拟坐标转换为屏幕坐标
  const screenX = (element.x - viewport.x) * viewport.scale;
  const screenY = (element.y - viewport.y) * viewport.scale;
  const screenWidth = element.width * viewport.scale;
  const screenHeight = element.height * viewport.scale;

  return (
    <Rect
      ref={rectRef}
      id={element.id}
      x={screenX}
      y={screenY}
      width={screenWidth}
      height={screenHeight}
      fill={element.fill}
      stroke={isSelected ? '#007bff' : element.stroke}
      strokeWidth={(isSelected ? 3 : element.strokeWidth)}
      rotation={element.rotation}
      draggable={mode === 'edit'}
      onClick={handleClick}
      onTap={handleClick}
      onDragEnd={handleDragEnd}
      shadowColor={isSelected ? '#007bff' : undefined}
      shadowBlur={isSelected ? 10 : 0}
      shadowOpacity={isSelected ? 0.3 : 0}
    />
  );
});

RectangleElement.displayName = 'RectangleElement';

export default RectangleElement;