import React from 'react';
import useCanvasStore from '../store/canvasStore';

const PropertyPanel: React.FC = () => {
  const { getSelectedElements, updateElement, removeElement } = useCanvasStore();
  const selectedElements = getSelectedElements();

  if (selectedElements.length === 0) return null;

  const element = selectedElements[0]; // 只显示第一个选中元素的属性

  const handlePropertyChange = (property: string, value: any) => {
    updateElement(element.id, { [property]: value });
  };

  const handleDelete = () => {
    selectedElements.forEach((el: any) => removeElement(el.id));
  };

  return (
    <div className="property-panel">
      <h3>属性面板</h3>
      
      <div className="property-item">
        <label>X 坐标:</label>
        <input
          type="number"
          value={Math.round(element.x)}
          onChange={(e) => handlePropertyChange('x', parseFloat(e.target.value) || 0)}
        />
      </div>

      <div className="property-item">
        <label>Y 坐标:</label>
        <input
          type="number"
          value={Math.round(element.y)}
          onChange={(e) => handlePropertyChange('y', parseFloat(e.target.value) || 0)}
        />
      </div>

      <div className="property-item">
        <label>宽度:</label>
        <input
          type="number"
          value={Math.round(element.width)}
          min="5"
          onChange={(e) => handlePropertyChange('width', Math.max(5, parseFloat(e.target.value) || 5))}
        />
      </div>

      <div className="property-item">
        <label>高度:</label>
        <input
          type="number"
          value={Math.round(element.height)}
          min="5"
          onChange={(e) => handlePropertyChange('height', Math.max(5, parseFloat(e.target.value) || 5))}
        />
      </div>

      <div className="property-item">
        <label>填充颜色:</label>
        <input
          type="color"
          className="color-input"
          value={element.fill}
          onChange={(e) => handlePropertyChange('fill', e.target.value)}
        />
      </div>

      <div className="property-item">
        <label>边框颜色:</label>
        <input
          type="color"
          className="color-input"
          value={element.stroke}
          onChange={(e) => handlePropertyChange('stroke', e.target.value)}
        />
      </div>

      <div className="property-item">
        <label>边框宽度:</label>
        <input
          type="number"
          value={element.strokeWidth}
          min="0"
          max="10"
          onChange={(e) => handlePropertyChange('strokeWidth', parseFloat(e.target.value) || 0)}
        />
      </div>

      <div className="property-item">
        <label>旋转角度:</label>
        <input
          type="number"
          value={Math.round(element.rotation)}
          onChange={(e) => handlePropertyChange('rotation', parseFloat(e.target.value) || 0)}
        />
      </div>

      <div className="property-item">
        <button 
          onClick={handleDelete}
          style={{
            width: '100%',
            padding: '8px',
            background: '#dc3545',
            color: 'white',
            border: 'none',
            borderRadius: '4px',
            cursor: 'pointer',
            marginTop: '10px'
          }}
        >
          删除元素
        </button>
      </div>

      {selectedElements.length > 1 && (
        <div style={{ marginTop: '10px', fontSize: '12px', color: '#666' }}>
          已选中 {selectedElements.length} 个元素
        </div>
      )}
    </div>
  );
};

export default PropertyPanel;