import React, { useRef, useEffect, useState } from 'react';
import { Stage } from 'react-konva';
import Konva from 'konva';
import useCanvasStore from '../store/canvasStore';
import CanvasLayer from './CanvasLayer';
import GridLayer from './GridLayer';


const CanvasStage: React.FC = () => {
  const stageRef = useRef<Konva.Stage>(null);
  const [stageSize, setStageSize] = useState({ width: 0, height: 0 });
  
  const { 
    mode, 
    viewport, 
    setViewport, 
    clearSelection,
    showGrid
  } = useCanvasStore();

  // 更新舞台尺寸
  useEffect(() => {
    const updateSize = () => {
      const container = stageRef.current?.container()?.parentElement;
      if (container) {
        const rect = container.getBoundingClientRect();
        const newSize = {
          width: rect.width || window.innerWidth,
          height: rect.height || window.innerHeight - 44 // 减去工具栏高度
        };
        console.log('Canvas size updated:', newSize);
        setStageSize(newSize);
        
        // 同时更新视口尺寸
        setViewport({
          width: newSize.width,
          height: newSize.height
        });
      } else {
        // 使用窗口尺寸作为默认值
        const newSize = {
          width: window.innerWidth,
          height: window.innerHeight - 44
        };
        setStageSize(newSize);
        setViewport({
          width: newSize.width,
          height: newSize.height
        });
      }
    };

    // 延迟执行确保DOM已渲染
    setTimeout(updateSize, 100);
    window.addEventListener('resize', updateSize);
    return () => window.removeEventListener('resize', updateSize);
  }, [setViewport]);

  // 处理舞台拖拽（仅浏览模式）
  const handleStageDragEnd = (e: Konva.KonvaEventObject<DragEvent>) => {
    if (mode === 'browse') {
      const stage = e.target as Konva.Stage;
      // 更新视口在虚拟空间中的位置
      // Stage的x,y是屏幕坐标，需要转换为虚拟坐标
      const deltaX = stage.x() / viewport.scale;
      const deltaY = stage.y() / viewport.scale;
      
      setViewport({
        x: viewport.x - deltaX,
        y: viewport.y - deltaY
      });
      
      // 重置Stage位置，保持在原点
      stage.position({ x: 0, y: 0 });
    }
  };

  // 处理缩放
  const handleWheel = (e: Konva.KonvaEventObject<WheelEvent>) => {
    e.evt.preventDefault();
    
    const stage = stageRef.current;
    if (!stage) return;

    const scaleBy = 1.1;
    const oldScale = viewport.scale;
    const pointer = stage.getPointerPosition();
    
    if (!pointer) return;

    // 将鼠标位置转换为虚拟坐标
    const mouseVirtualX = pointer.x / oldScale + viewport.x;
    const mouseVirtualY = pointer.y / oldScale + viewport.y;

    let newScale = e.evt.deltaY > 0 ? oldScale / scaleBy : oldScale * scaleBy;
    
    // 限制缩放范围
    newScale = Math.max(0.1, Math.min(5, newScale));

    // 计算新的视口位置，保持鼠标位置在虚拟空间中不变
    const newViewportX = mouseVirtualX - pointer.x / newScale;
    const newViewportY = mouseVirtualY - pointer.y / newScale;

    setViewport({
      scale: newScale,
      x: newViewportX,
      y: newViewportY
    });
  };

  // 处理舞台点击（清除选择）
  const handleStageClick = (e: Konva.KonvaEventObject<MouseEvent>) => {
    // 如果点击的是舞台本身（不是元素），清除选择
    if (e.target === e.target.getStage()) {
      clearSelection();
    }
  };

  return (
    <>
      <Stage
        ref={stageRef}
        width={stageSize.width}
        height={stageSize.height}
        x={0}
        y={0}
        scaleX={1}
        scaleY={1}
        draggable={mode === 'browse'}
        onDragEnd={handleStageDragEnd}
        onWheel={handleWheel}
        onClick={handleStageClick}
        onTap={handleStageClick}
      >
        {showGrid && <GridLayer />}
        <CanvasLayer />
      </Stage>
      
      <div className="canvas-info">
        {mode === 'browse' ? '浏览' : '编辑'} | {Math.round(viewport.scale * 100)}% | ({Math.round(viewport.x)}, {Math.round(viewport.y)})
      </div>
    </>
  );
};

export default CanvasStage;